* {
    box-sizing: border-box;
}

.Home {
    height: 100%;
}

.home-background {
    height: 825px;
    width: 100%;
    margin-bottom: -6px;
    z-index: 1;
}

.home-title {
    color: cyan;
    font-size: 28px;
    font-weight: 900;
    letter-spacing: 4px;
    line-height: 42px;
    font-family: 'Noto Sans JP', sans-serif;
    position: absolute;
    top: 180px;
    margin-left: 200px;
    margin-bottom: 90px;
    z-index: 2;
}

.home-dot-under-button {
    background-color: cyan;
    height: 6px;
    width: 6px;
    border-radius: 6px;
    font-size: 200px;
    top: 220px;
    right: 833px;
    position: absolute;
}

.home-nav-list {
    list-style: none;
    display: block;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1.4px;
    line-height: 25.2px;
    -webkit-font-smoothing: antialiased;
    position: absolute;
    text-align: right;
    height: 58px;
    top: 400px;
    right: 0px;
    z-index: 2;
}

.home-nav-list li {
    display: inline-block;
    padding-top: 30px;
    margin-right: 67px;
}

.home-link-list {
    list-style: none;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1.4px;
    line-height: 25.2px;
    -webkit-font-smoothing: antialiased;
    position: absolute;
    text-align: right;
    top: 480px;
    right: 15px;
    padding-left: 0;
    z-index: 2;
}

.home-link-list li {
    display: inline-block;
    padding-top: 60px;
    margin-right: 50px;
}